var marked = require('../../dep/marked.js');

// markdown  html
var renderer = new marked.Renderer();
var options = {
    renderer: renderer
};

/**
 * 
 *
 * @param {string} href 
 * @param {string} title  title
 * @param {string} text 
 *
 * @returns {string}   
 */
renderer.link = function (href, title, text) {
    var attrStr = ''
        + ' href="' + href + '"'
            // + ' title="' + title + '"'
        + ' target="_blank"';
    return '<a' + attrStr + '>' + text + '</a>';
};

/********************  ********************/
var headerIdPrefix = 'header-'; //  id 
// 
// 
var rootNode = {
    text: '',
    level: 0,
    children: [],
    id: headerIdPrefix,
    parentNode: null
};
// 
// 
var preNode = rootNode;
/**
 * 
 *  rootNode 
 *
 * @param {string} text 
 * @param {Number} level 
 *
 * @returns {string}  h1-h6 
 */
renderer.heading = function (text, level) {
    var html;
    var preLevel = preNode.level;
    var currentNode;
    // ()
    if (level === preLevel) {
        currentNode = createHeaderNode(text, level, preNode.parentNode);
    }
    // ()
    else if (level > preLevel) {
        while (level - preLevel > 0) {
            currentNode = createHeaderNode(null, ++preLevel, preNode);
        }
        currentNode.text = text;
    }
    // 
    else {
        // 
        while (level < preNode.level) {
            //  preNode 
            preNode = preNode.parentNode;
        }
        currentNode = createHeaderNode(text, level, preNode.parentNode);
    }

    html = '<h' + level + ' id="' + currentNode.id + '">'
        + text
        + '</h' + level + '>\n';
    return html;
};

/**
 * 
 *
 * @param {string|null} text 
 *                            null 
 *                           ()
 *                           
 * @param {Number} level 
 * @param {Object} parentNode 
 *
 * @returns {Object} newNode  
 */
function createHeaderNode(text, level, parentNode) {
    var idSpace = parentNode.level === 0 ? '' : '-';
    var newNode = {
        level: level,
        id: parentNode.id + idSpace + (parentNode.children.length + 1),
        children: [],
        parentNode: parentNode
    };
    parentNode.children.push(newNode);
    if (text !== null) {
        newNode.text = text;
    }
    preNode = newNode;
    return newNode;
}

/**
 *  markdown 
 *
 * @param {string} mdContent md 
 *
 * @returns {Object} html  
 *                   {htmlContent: string, headerTree: Array}
 */
function mark(mdContent) {
    // 
    rootNode.children = [];
    var htmlContent = marked(mdContent, options);
    return {
        htmlContent: htmlContent,
        headerTree: rootNode.children
    };
}

module.exports = {
    mark: mark
};


